* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --dark-blue: #060b35;
  --light-blue: #00acdc;
  --back-color: #f3f3f3;
  --white: #fff;
  --grey: #616070;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(n) {
  background-image: url("star-active.edda4c43.svg");
}

.product {
  border: 1px solid var(--back-color);
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.product__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.product__title-text {
  color: var(--dark-blue);
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code-text {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__stars {
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 24px;
  display: flex;
}

.product__reviews-text {
  color: var(--dark-blue);
  align-self: flex-start;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.product__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.product__price-text {
  color: var(--grey);
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product__price-value {
  color: var(--dark-blue);
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__button {
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  font-style: 14px;
  color: var(--white);
  background-color: var(--light-blue);
  border: 1px solid var(--light-blue);
  border-radius: 5px;
  width: 100%;
  height: 40px;
  text-decoration: none;
}

.product__button:hover {
  background-color: var(--white);
  color: var(--light-blue);
}



